*{
    box-sizing: border-box;
}
.container{
    background-color: rgba(49, 7, 6, 0.932);
    margin: auto;
    width: 50vw;
    /* height: 80vh;  */
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* align-self: center;
    justify-self: center; */
    position: fixed;
    top: 15vh;
    left: 18%;
    width: 60%;
    z-index: 100;
    overflow: hidden;
}
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.822);
  }